<template>
  <div class="login-container">
    <div class="login-card">
      <h2>Iniciar sesión</h2>
      <form @submit.prevent="login">
        <input type="text" v-model="email" placeholder="Correo electrónico" required />
        <input type="password" v-model="password" placeholder="Contraseña" required />
        <button type="submit">Entrar</button>
        <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
      </form>
      <div>
        <hr>
        <router-link to="/register" class="register-button">Crear usuario nuevo</router-link>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'LoginComponent',
  data() {
    return {
      email: '',
      password: '',
      errorMessage: '',
    };
  },
  methods: {
    async login() {
      try {
        const response = await axios.post('http://localhost:8000/api/login', {
          email: this.email,
          password: this.password,
        });
        const token = response.data.token;
        localStorage.setItem('token', token);
        this.$router.push('/');
      } catch (error) {
        this.errorMessage = 'Credenciales inválidas o error del servidor';
        console.error(error);
      }
    },
  },
};
</script>

<style scoped>
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a1a, #333);
}

.login-card {
  background-color: #2c2c2c;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
  width: 200%;
  max-width: 400px;
  text-align: center;
}

h2 {
  font-size: 5rem;
  margin-bottom: 2rem;
  color: #fff;
}

input {
  width: 100%;
  padding: 0.75rem;
  margin: 0.5rem 0;
  border: none;
  border-radius: 8px;
  background-color: #444;
  color: white;
  font-size: 2rem;
}

input::placeholder {
  color: #ccc;
}

button {
  width: 100%;
  padding: 0.75rem;
  margin-top: 1rem;
  background-color: #4caf50;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

.register-button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.75rem;
  background-color: #4caf50;
  color: white;
  font-weight: bold;
  border-radius: 8px;
  text-decoration: none;
  transition: 0.3s ease;
  font-size: 1.8rem;
}

.register-button:hover {
  background-color: #45a049;
}

.error {
  color: #ff4c4c;
  margin-top: 1rem;
}
</style>
