<template>
  <div class="container">
    <h1>Checkout Form</h1>
    <form class="form cf">
      <section class="plan cf">
        <h2>Elige un plan:</h2>

        <div style="display: flex;">
          <input
            type="radio"
            name="plan"
            id="basic"
            value="basic"
            v-model="selectedPlan"
            @change="pagar"
          />
          <label class="basic-label four col" for="basic">Básico ($200)</label>

          <input
            type="radio"
            name="plan"
            id="premium"
            value="premium"
            v-model="selectedPlan"
            @change="pagar"
          />
          <label class="premium-label four col" for="premium">Premium($400)</label>

          <input
            type="radio"
            name="plan"
            id="vip"
            value="vip"
            v-model="selectedPlan"
            @change="pagar"
          />
          <label class="vip-label four col" for="vip">VIP ($600)</label>
        </div>
      </section>

      <div id="btnPayPal"></div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'CheckoutComponent',
  data() {
    return {
      selectedPlan: 'basic',
      planPrices: {
        basic: 200,
        premium: 400,
        vip: 600,
      },
    };
  },
  mounted() {
    this.pagar();
  },
  methods: {
    pagar() {
      const btn = document.getElementById('btnPayPal');
      if (btn) btn.innerHTML = '';

      if (window.paypal && typeof window.paypal.Buttons === 'function') {
        window.paypal.Buttons({
          createOrder: (data, actions) => {
            const price = this.planPrices[this.selectedPlan];
            return actions.order.create({
              purchase_units: [
                {
                  amount: {
                    value: price.toFixed(2),
                    currency_code: 'MXN',
                  },
                  description: `Pago del plan ${this.selectedPlan.toUpperCase()}`,
                },
              ],
            });
          },

          onApprove: async (data, actions) => {
            const details = await actions.order.capture();

            alert(`¡Pago completado, ${details.payer.name.given_name}! Gracias por comprar el plan ${this.selectedPlan.toUpperCase()}.`);

            const planMap = {
              basic: 1,
              premium: 2,
              vip: 3,
            };
            const planId = planMap[this.selectedPlan];

            try {
              const token = localStorage.getItem('token');
              await fetch('http://localhost:8000/api/users/plan', {
                method: 'PUT',
                headers: {
                  'Content-Type': 'application/json',
                  'Authorization': `Bearer ${token}`,
                },
                body: JSON.stringify({ plan_id: planId }),
              });

              console.log('Plan actualizado en el backend:', planId);

              // ✅ Redirigir al login después del pago
              window.location.href = '/login'; // Ajusta si tu ruta es diferente

            } catch (error) {
              console.error('Error al actualizar el plan en el servidor:', error);
            }
          },
        }).render('#btnPayPal');
      } else {
        console.error('PayPal SDK no cargado correctamente.');
      }
    },
  },
};
</script>

<style scoped>
.container {
  max-width: 600px;
  margin: 2rem auto;
  background: #1e1e1e;
  padding: 2rem;
  border-radius: 12px;
  color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
}

h1 {
  text-align: center;
  margin-bottom: 1.5rem;
}

.plan {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

label {
  display: block;
  padding: 1rem;
  border: 1px solid #444;
  border-radius: 8px;
  cursor: pointer;
  background-color: #2c2c2c;
}

input[type='radio'] {
  display: none;
}

input[type='radio']:checked + label {
  background-color: #4caf50;
  border-color: #4caf50;
  color: white;
}

#btnPayPal {
  margin-top: 2rem;
  text-align: center;
}
</style>
