<template>
    <div class="product-details">
      <h1>{{ product.title }}</h1>
      <img :src="product.image" class="product-image" alt="Product Image">
      <p>{{ product.description }}</p>
      <div class="meta">
        <div><strong>IMDB:</strong> {{ product.imdbRating }}</div>
        <div><strong>Año:</strong> {{ product.releaseYear }}</div>
      </div>
      <router-link to="/">Volver a inicio</router-link>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    name: 'ProductSingleComponent',
    props: ['id'], // Recibe el 'id' de la película o serie desde la URL
    data() {
      return {
        product: {} // Aquí se guardarán los detalles de la película o serie
      };
    },
    mounted() {
  // Asegúrate de usar 'movieId' (con "I" mayúscula)
  const movieId = this.$route.params.id;

  // Llamar al método con el movieId correcto
  this.fetchProductDetails(movieId);
},
methods: {
  fetchProductDetails() {
    const movieId = this.$route.params.id; // Asegúrate de que `id` sea el nombre del parámetro en la URL
    axios.get(`http://localhost:8000/api/movie/${movieId}`)
      .then(response => {
        this.movie = response.data; // Asumiendo que la respuesta tiene los datos de la película
      })
      .catch(error => {
        console.error('Error al obtener los detalles del producto:', error);
      });
  }
},
  };
  </script>
  
  <style scoped>
  .product-details {
    padding: 20px;
    text-align: center;
  }
  
  .product-image {
    max-width: 100%;
    height: auto;
  }
  
  .meta {
    margin-top: 10px;
  }
  </style>
  