<template>
  <header>
    <div class="netflixLogo">
      <router-link id="logo" to="/"><img :src="logoUrl" alt="Logo Image" /></router-link>
    </div>      

    <nav class="main-nav">                
      <a href="#home">Películas</a>
      <a href="#tvShows">Series</a>
      <a href="#movies">Últimos Agregados</a>
    </nav>

    <nav class="sub-nav">
      <a href="#"><i class="fas fa-search sub-nav-logo"></i></a>
      <a href="#"><i class="fas fa-bell sub-nav-logo"></i></a>
      <a href="#">Account</a>
      <button @click="logout" class="logout-btn">Cerrar sesión</button>
    </nav>      
  </header>
</template>

<script>
export default {
  name: 'HeaderComponent',
  data() {
    return {
      logoUrl: require('@/assets/tecflix.png')
    };
  },
  methods: {
    logout() {
      localStorage.removeItem('token');
      this.$router.push({ name: 'login' });
    }
  }
};
</script>

