import { createRouter, createWebHashHistory } from 'vue-router';
import HomeComponent from '@/views/home/HomeComponent.vue';
import ProductSingleComponent from '@/views/products/ProductSingleComponent.vue';
import CheckoutComponent from '@/views/checkout/CheckoutComponent.vue';
import LoginComponent from '@/views/login/LoginComponent.vue';   
import RegisterView from '@/views/RegisterView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeComponent,
  },
  {
    path: '/movie/:id',
    name: 'product',
    component: ProductSingleComponent,
    props: true,
  },
  {
    path: '/checkout',
    name: 'checkout',
    component: CheckoutComponent,
  },
  {
    path: '/login',
    name: 'login',
    component: LoginComponent,
  },
  {
    path: '/register',
    name: 'Register',
    component: RegisterView,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
